<template class="side-nav">
	<div>
		<div v-for="(item,i) in items" >
			<el-submenu v-if="item.children!=undefined && item.children.length>0" :index="item.id+''">
				<!--创建父级菜单-->
				<template slot="title">
					<i :class="item.icon">{{item.name}}</i>
				</template>
				<!--创建子菜单-->
				<nav-bar-item :items='item.children'></nav-bar-item>
			</el-submenu>
			<el-menu-item v-else :index="item.id+''"  @click="$router.push(item.path)">
				<i :class="item.icon">{{item.name}}</i>
			</el-menu-item>
		</div>
	</div>
</template>
<script>
	export default{
		name:"NavBarItem",
		props:["items","Navindex"]
	}

</script>


