<template>
	<!-- <el-aside style="width:200px;" class="indexaside"> -->
		<div style="width:200px">
			<el-menu
		      :default-active="defaultactive.toString()"
		      class="el-menu-vertical-demo"
		      @open="handleOpen"
		      @close="handleClose"
		      @select="handleSelect"
		      background-color="#324157"
		      text-color="#bfcbd9"
		      active-text-color="#20a0ff">
				<nav-bar-item :items="newMenuData"></nav-bar-item>
		    </el-menu>
	    </div>
	<!-- </el-aside> -->
</template>
<script>
	import NavBarItem from "./NavBarItem";
	export default{
		data(){
			return {
				defaultactive:"1",
				chooseList:[],			//选择的列表
				newMenuData:[
					{
						id:"1",				//id要用字符串的形式 ,因为是拿来做为高亮的
						name:"首页",
						icon:"el-icon-document",
						path:"/index",
						pathname:"index"
					},
					{
						id:"2",
						name:"数据管理",
						icon:"el-icon-printer",
						children:[
							{
								id:"2-1",
								name:"用户列表",
								icon:"el-icon-printer",
								path:"/userlist",
								pathname:"userlist",
								children:[
									{
										id:"2-1-1",
										name:"用户子列表",
										icon:"el-icon-printer",
										path:"/orderlist",
										pathname:"orderlist"
									}
								]
							},
							{
								id:"2-2",
								name:"食品列表",
								icon:"el-icon-printer",
								path:"/foodlist",
								pathname:"foodlist"
							}
						]
					},
					{
						id:"3",
						name:"添加数据",
						icon:"el-icon-plus",
						children:[
							{
								id:"3-1",
								name:"添加商铺",
								icon:"el-icon-plus",
								path:"/addshop",
								pathname:"test"
							}
						]
					}
				]
			}
		},
		methods:{
			handleOpen(key,keyPath){
				
			},
			handleClose(key,keyPath){
				
			},
			handleSelect(key,keyPath){
				
			},
			currenMenuFun(pathname){
				var currentObj={};
				var jumpout=0;
				var that=this;

				var currentChooseObj=[];		//第一级装 path 和 name 的。
				var traverseCurrentChoose=[];

				function traverse(data,flag){
					if(flag){  				//这边是 递归遍历执行
						data.forEach(function(value,index){
							if(jumpout){return;}	//这边是。因为 return 只跳出当前的函数，所以要多个地方退出
							if(value.pathname==pathname){
								that.defaultactive=value.id;   //高亮文字

								traverseCurrentChoose.push({"name":value.name,"path":value.path});

								jumpout=1;
								return;
							}else if(value.children && value.children.length>0){
								traverseCurrentChoose.push({"name":value.name,"path":value.path});

								traverse(value.children,1)
							}else{
								traverseCurrentChoose=[];
							}
						})
					}else{
						data.forEach(function(value,index){
							if(jumpout){return;}     //这边是。因为 return 只跳出当前的函数，所以要多个地方退出
							currentObj=value;
							currentChooseObj=[];			//清空
							if(value.pathname==pathname){
								that.defaultactive=value.id;     //高亮文字
								currentChooseObj.push({"name":value.name,"path":value.path})
								jumpout=1;
								return;
							}else if(value.children && value.children.length>0){
								currentChooseObj.push({"name":value.name,"path":value.path})
								traverse(value.children,1)
							}
						})
					}
				}
				traverse(this.newMenuData);
				if(jumpout){		//表示退出循环后的找到对应的name，找到当前的 对象
					//console.log(currentObj)				//当前的哪一个对象被选中
					//console.log(currentChooseObj);


					that.chooseList=currentChooseObj.concat(traverseCurrentChoose);				//=========>当前选中的面包屑 是个对象
					console.log(currentChooseObj.concat(traverseCurrentChoose));	 //当前
				}
			},
		},
		created(){
			var path=this.$route.name;
			this.currenMenuFun(path);
		},
		watch:{
			"$route"(to,from){
				var path=to.name;

			}
		},
		components:{NavBarItem}
	}
</script>
<style>
	

</style>


